// Step 1: Import React
import * as React from 'react'
import {contentContainer, more} from '../styles/portfolio.module.css'
import PortfolioLayout from '../components/portfolioLayout'
import ContentThumbnail from '../components/contentThumbnail'
import { StaticImage } from 'gatsby-plugin-image'
import { Link } from "gatsby"
import Blurb from '../components/blurb'

// Step 2: Define your component
const Portfolio = () => {
  return (
    <PortfolioLayout pageTitle={"Portfolio"} subPageTitle={"gallery"} >
      <Blurb>
        I like to record all projects relevant to my interests. Here, you will find projects in software development, programming, and gamedev, but also my work in GIS, art, and design. Feel free to look through any of them. I am constantly updating with my new initiatives!
      </Blurb>
      <div className={contentContainer}>
        <Link to="/portfolio-pages/programming" >
          <ContentThumbnail title="Programming" keyname="programming" alt="A picture of some code"/>
        </Link>
        <Link to='/portfolio-pages/GIS'>
          <ContentThumbnail title="GIS" keyname="GIS" alt="An image of a map"/>
        </Link>
        <Link to='/portfolio-pages/gamedev'>
          <ContentThumbnail title="Gamedev" keyname="Gamedev" alt="A pixellated game map"/>
        </Link>
        <Link to='/portfolio-pages/live2D'>
          <ContentThumbnail title="Live2D" keyname="Live2D" alt="A live2D model of a steller's jay"/>
        </Link>
        <Link to='/portfolio-pages/UI-UX'>
          <ContentThumbnail title="UI/UX Design" keyname="UIUX" alt="A picture of a cool component"/>
        </Link>
        <div className={more}>
          <StaticImage
              src = "../images/magpie.png"
              alt="a cartoon image of a Magpie saying More to Come"
              placeholder="blurred"
              layout="constrained"
          />
        </div>
      </div>
    </PortfolioLayout>
  )
}
// Step 3: Export your component
export default Portfolio